import React, { Component, Suspense, lazy } from 'react';
import { Link, HashRouter as Router, Route } from "react-router-dom";
import Loder from '../Loder';
import PageFooter from '../PageFooter';
import "./index.less";

const GoJSintro = lazy(() => import('./Intro'));
const GoJSfirst = lazy(() => import('./First'));
const GoJSsecond = lazy(() => import('./Second'));
const GoJSthird = lazy(() => import('./Third'));
const GoJSforth = lazy(() => import('./Forth'));

export default class index extends Component {
    render() {
        return (
            <div>
                <Router>
                    <Suspense fallback={<Loder />}>
                        <Route exact path="/gojs/0" render={() => (<GoJSintro />)} />
                        <Route exact path="/gojs/1" render={() => (<GoJSfirst />)} />
                        <Route exact path="/gojs/2" render={() => (<GoJSsecond />)} />
                        <Route exact path="/gojs/3" render={() => (<GoJSthird />)} />
                        <Route exact path="/gojs/4" render={() => (<GoJSforth />)} />
                        <Route exact path="/gojs" render={() => (
                            <div className="catalog">
                                <div className="container">
                                    <div className="title">
                                        <h1>GoJS教程</h1>
                                    </div>
                                    <div className="contents">
                                        <ul>
                                            <li><Link to="/gojs/0">介绍</Link></li>
                                            <li><Link to="/gojs/1">第一节 简单的GoJS demo</Link></li>
                                            <li><Link to="/gojs/2">第二节 GoJS应用中有什么</Link></li>
                                            <li><Link to="/gojs/3">第三节 使用GoJS构建Parts(Parts 和 Panels)</Link></li>
                                            <li><Link to="/gojs/4">第四节 GoJS Diatram Model</Link></li>
                                            <li><Link to="/gojs/5">第五节 使用GoJS构建Parts(Templates)</Link></li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        )} />
                        <PageFooter />
                    </Suspense>
                </Router>
            </div >
        )
    }
}
